
import { Options, Vue } from "vue-class-component";
import roomApi from "@/configs/roomApi";
import userApi from "@/configs/userApi";
import gameApi from "@/configs/gameApi";
import CenterLayout from "@/layout/CenterLayout.vue";

@Options({
  name: "RoomDetailView",
  components: {
    CenterLayout,
  },
  created() {
    this.getRoomDetail();
    this.getUsers();
    const { roomId } = this.$route.params;
    this.roomId = roomId;
  },
  data() {
    return {
      games: [],
      dialogVisible: false,
      users: [],
      newUser: {
        name: "",
        id: "",
        description: "",
      },
      roomDetail: {
        name: "",
        id: "",
        description: "",
        games: [],
      },
    };
  },
  computed: {
    tableData() {
      return this.games;
    },
  },
  methods: {
    handleClose() {
      this.dialogVisible = false;
    },
    onSaveGameResult(gameId: string) {
      console.log(gameId);
      const { callNumber, inventory } = this.games.find(
        (item: any) => item._id === gameId
      );
      this.httpRequest
        .post(gameApi.updateGameResult(gameId), { callNumber, inventory })
        .then((res) => {
          alert(JSON.stringify(res.data));
          this.getRoomDetail();
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    getRoomDetail() {
      const { roomId } = this.$route.params;
      this.httpRequest
        .get(roomApi.getRoomDetail(roomId))
        .then((res) => {
          this.roomDetail = res.data;
          this.games = this.roomDetail.games;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    deleteGameInRoom(gameId: string) {
      this.httpRequest
        .delete(gameApi.deleteGame(gameId))
        .then((res) => {
          alert(JSON.stringify(res.data));
          this.getRoomDetail();
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    getUsers() {
      this.httpRequest
        .get(userApi.getUserAll)
        .then((res) => {
          this.users = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    onAddGametoRoom() {
      this.httpRequest
        .post(roomApi.addGameToRoom(this.roomId, this.newUser.id))
        .then((res) => {
          this.dialogVisible = false;
          alert(JSON.stringify(res.data));
          this.getRoomDetail();
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
})
export default class RoomDetailView extends Vue {
  tableData: unknown;
  dialogVisible: any;
  users: any;
  newUser: any;
  onAddGametoRoom: any;
  onSaveGameResult: any;
  deleteGame: any;
  roomDetail: any;
  games: any;
  deleteGameInRoom: any;
}
