
import { Options, Vue } from "vue-class-component";
import permissionApi from "@/configs/permissionApi";

@Options({
  name: "PermissionEditModal",
  methods: {
    handleClose() {
      this.$emit("close");
    },
    submitEdit() {
      this.onUpdatePermission();
      this.handleClose();
    },
    onUpdatePermission() {
      const permission = this.permissionDetail;
      this.httpRequest
        .post(permissionApi.editPermission(permission.id), permission)
        .then((res) => {
          alert(JSON.stringify(res.data));
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
  props: {
    permissionDetail: {
      name: {
        type: String,
        required: true,
        default: "",
      },
      id: {
        type: String,
        required: true,
        default: "",
      },
      description: {
        type: String,
        required: true,
        default: "",
      },
    },
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
})
export default class PermissionEditModal extends Vue {
  handleClose: any;
  permissionDetail: any;
  submitEdit: any;
  dialogVisible: any;
}
