
import roomApi from "@/configs/roomApi";
import userApi from "@/configs/userApi";
import AddRoom from "@/components/AddRoom.vue";
import { Vue, Options } from "vue-class-component";
import CenterLayout from "@/layout/CenterLayout.vue";
import RoomEditModal from "@/components/room/RoomEditModal.vue";

@Options({
  name: "RoomView",
  computed: {},
  components: {
    AddRoom,
    CenterLayout,
    RoomEditModal,
  },
  methods: {
    getRooms() {
      this.httpRequest
        .get(roomApi.getRoomAll)
        .then((res) => {
          this.tableData = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    deleteRoom(RoomId: string) {
      this.httpRequest
        .delete(roomApi.deleteRoom(RoomId))
        .then((res) => {
          this.getRooms();
          this.tableData = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    getUsers() {
      this.httpRequest
        .get(userApi.getUserAll)
        .then((res) => {
          this.users = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
  created() {
    this.getRooms();
  },
  data() {
    return {
      tableData: [],
      users: [],
    };
  },
})
export default class Role extends Vue {
  Rooms?: Array<any>;
  handleTabClick: any;
  RoomId: any;
  tableData:
    | Array<{ description: string; name: string; id: string }>
    | undefined;
  deleteRoom: any;
  rooms: Array<{ name: string; id: string; description: string }> = [];
  visibleEditRoom = false;
  roomDetail: { id: string; name: string; description: string } = {
    name: "",
    description: "",
    id: "",
  };
}
