
import { Options, Vue } from "vue-class-component";
@Options({
  name: "UserDashBoard",
  props: {
    filter: {
      type: Array,
      default: () => [],
    },
  },
  mounted() {
    console.log(this.$props);
  },
  computed: {
    dashboard() {
      const statistics = this.filter;

      return statistics.map((item) => ({
        total: parseInt(item.totalScore),
        name: item._id.name,
      }));
    },
  },
})
export default class UserDashBoard extends Vue {
  statistic: any;
  dashboard: any;
}
