
import { Options, Vue } from "vue-class-component";
import roomApi from "@/configs/roomApi";

@Options({
  name: "RoomEditModal",
  methods: {
    handleClose() {
      this.$emit("close");
    },
    submitEdit() {
      this.onUpdateRoom();
      this.handleClose();
    },
    onUpdateRoom() {
      const room = this.roomDetail;
      this.httpRequest
        .post(roomApi.editRoom(room.id), room)
        .then((res) => {
          alert(JSON.stringify(res.data));
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
  props: {
    roomDetail: {
      name: {
        type: String,
        required: true,
        default: "",
      },
      id: {
        type: String,
        required: true,
        default: "",
      },
      description: {
        type: String,
        required: true,
        default: "",
      },
    },
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
})
export default class RoomEditModal extends Vue {
  handleClose: any;
  roomDetail: any;
  submitEdit: any;
  dialogVisible: any;
}
