
import roundApi from "@/configs/roundApi";
import userApi from "@/configs/userApi";
import AddRound from "@/components/AddRound.vue";
import { Vue, Options } from "vue-class-component";
import Room from "./ui/RoomCard.vue";
import CenterLayout from "@/layout/CenterLayout.vue";

@Options({
  name: "Round",
  computed: {},
  components: {
    AddRound,
    Room,
    CenterLayout,
  },
  methods: {
    getRounds() {
      this.httpRequest
        .get(roundApi.getRoundAll)
        .then((res) => {
          this.tableData = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    deleteRound(roundId: string) {
      this.httpRequest
        .delete(roundApi.deleteRound(roundId))
        .then((res) => {
          this.getRounds();
          this.tableData = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
    getUsers() {
      this.httpRequest
        .get(userApi.getUserAll)
        .then((res) => {
          this.users = res.data;
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
  created() {
    this.getRounds();
  },
  data() {
    return {
      tableData: [],
      users: [],
    };
  },
})
export default class Home extends Vue {
  rounds?: Array<any>;
  handleTabClick: any;
  roundId: any;
  tableData:
    | Array<{ description: string; name: string; id: string }>
    | undefined;
  deleteRound: any;
  rooms: Array<{ name: string; id: string; description: string }> = [];
}
