
import { Options, Vue } from "vue-class-component";
import roleApi from "@/configs/roleApi";

@Options({
  name: "RoleEditModal",
  methods: {
    handleClose() {
      this.$emit("close");
    },
    submitEdit() {
      this.onUpdateRole();
      this.handleClose();
    },
    onUpdateRole() {
      const role = this.roleDetail;
      this.httpRequest
        .post(roleApi.editRole(role.id), role)
        .then((res) => {
          alert(JSON.stringify(res.data));
        })
        .catch((err) => {
          alert(JSON.stringify(err));
        });
    },
  },
  props: {
    roleDetail: {
      name: {
        type: String,
        required: true,
        default: "",
      },
      id: {
        type: String,
        required: true,
        default: "",
      },
      description: {
        type: String,
        required: true,
        default: "",
      },
    },
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
})
export default class RoleEditModal extends Vue {
  handleClose: any;
  roleDetail: any;
  submitEdit: any;
  dialogVisible: any;
}
